import { Helmet } from "react-helmet";

const mystyle = {
  color: "#203A43",
  padding: "40px",
  fontFamily: "fantasy",
  textAlign: "center"
};

const title = {
  background: "linear-gradient(#ddd6f3, #faaca8)",
  textDecoration: "underline overline"
};
var Home = () => {
  return (
    <>
      <div style={mystyle}>
        <Helmet>
          <style>
            {
              'body { background-image: url("https://png.pngtree.com/thumb_back/fh260/back_our/20190620/ourmid/pngtree-youth-does-not-graduate-decisive-battle-college-entrance-examination-poster-background-image_145949.jpg");background-position:center;background-size:cover;background-repeat:no-repeat; }'
            }
          </style>
        </Helmet>
        <h1 id="title" style={title}>
          Student Management
        </h1>
        <br />
        <h2
          style={{
            backgroundImage: "linear-gradient(#4568dc, #b06ab3)",
            fontFamily: "cursive",
            color: "white"
          }}
        >
          To see all students' name select student
        </h2>
        <br />
        <br />
        <h2
          style={{
            backgroundImage: "linear-gradient(#ff512f, #dd2476)",
            fontFamily: "cursive",
            color: "#EDE574"
          }}
        >
          Availabe in detail profile of Students
        </h2>
        <br />
        <br />
        <h2
          style={{
            backgroundImage: "linear-gradient(#EDE574, #E1F5C4)",
            fontFamily: "cursive",
            color: "#dd3e54"
          }}
        >
          Student profile available by department
        </h2>
        <br />
        <br />
      </div>
      <br />
      <br />
      <br />
      <br />
      <br />
      <br />
    </>
  );
};

export default Home;
