import { useEffect, useState } from "react";
import { useNavigate, useParams } from "react-router-dom";
import { Link } from "react-router-dom";
import Helmet from "react-helmet";

const table_head = {
  fontWeight: "bold",
  fontFamily: "cursive",
  fontSize: "30px"
};

const table_content = {
  fontFamily: "cursive",
  fontSize: "20px"
};

function StudentDetail() {
  let params = useParams();
  const navigate = useNavigate();

  const [student, setStudent] = useState({});

  useEffect(() => {
    fetch("https://62da4aad9eedb699636a73fe.mockapi.io/Student/" + params.id)
      .then((res) => {
        return res.json();
      })
      .then((res) => {
        setStudent(res);
      });
  }, []);

  return (
    <>
      <div>
        <Helmet>
          <style>
            {
              'body { background-image: url("https://png.pngtree.com/thumb_back/fh260/back_our/20190621/ourmid/pngtree-teacher-s-college-classroom-coaching-course-poster-background-image_188494.jpg");background-position:center;background-size:cover;background-repeat:no-repeat; }'
            }
          </style>
        </Helmet>
        <table
          border="3"
          align="center"
          style={{ backgroundColor: "#1e3d59", color: "yellow" }}
        >
          <tr>
            <td colSpan="3" align="center" style={table_head}>
              {student.StudentName}
            </td>
          </tr>
          <tr>
            <td colSpan="3" align="center">
              <img src={student.StudentImage} alt="asdf" />
            </td>
          </tr>
          <tr style={table_content}>
            <td>Student Mobile: </td>
            <td>{student.StudentMobile}</td>
          </tr>
          <tr style={table_content}>
            <td>Parent's Mobile: </td>
            <td>{student.StudentParentMobile}</td>
          </tr>
          <tr style={table_content}>
            <td>Email: </td>
            <td>{student.StudentEmail}</td>
          </tr>
          <tr style={table_content}>
            <td>Department: </td>
            <td>{student.StudentDepartment}</td>
          </tr>
          <tr>
            <td>
              <Link to={"../students"}>
                <button class="btn-hover">Back</button>
              </Link>
            </td>
            <td>
              <button
                class="btn-hoverD"
                onClick={() => {
                  fetch(
                    "https://62da4aad9eedb699636a73fe.mockapi.io/Student/" +
                      params.id,
                    {
                      method: "DELETE"
                    }
                  ).then((res) => {
                    navigate("/students");
                  });
                }}
              >
                Delete
              </button>
            </td>
            <td>
              <button
                class="btn-hoverE"
                onClick={() => {
                  navigate("/student/edit/" + params.id);
                }}
              >
                Edit
              </button>
            </td>
          </tr>
        </table>
      </div>
    </>
  );
}
export default StudentDetail;
