import { useState, useEffect } from "react";
import { Helmet } from "react-helmet";
import { useNavigate, useParams } from "react-router-dom";
const table_style = {
  marginLeft: "auto",
  marginRight: "auto",
  marginTop: "40px",
  backgroundImage: `url("https://img.freepik.com/free-vector/blue-pink-halftone-background_53876-99004.jpg?w=2000")`,
  fontFamily: "monospace",
  fontSize: "20px",
  borderColor: "orange",
  color: "#ff7f7f"
};

const table_header = {
  fontFamily: "papyrus,Fantasy",
  fontSize: "30px",
  color: "#35527b"
};
export default function StudentAdd() {
  const nevigate = useNavigate();
  const [data, setdata] = useState({});
  const params = useParams();

  useEffect(() => {
    if (params.id > 0) {
      fetch("https://62da4aad9eedb699636a73fe.mockapi.io/Student/" + params.id)
        .then((res) => {
          return res.json();
        })
        .then((res) => {
          setdata(res);
        });
    }
  }, []);

  return (
    <>
      <Helmet>
        <style>
          {
            'body { background-image: url("https://media.istockphoto.com/illustrations/education-illustration-id1159577042?k=20&m=1159577042&s=612x612&w=0&h=9DZMM2N4jekPz_5x8eSTcrIu4OSo3DB2wvBeiUuyiJo=");background-position:center;background-size:cover;background-repeat:no-repeat; }'
          }
        </style>
      </Helmet>
      <table border="3" style={table_style}>
        <tr>
          <td colSpan="3" align="center" style={table_header}>
            Add Student's Details
          </td>
        </tr>
        <tr>
          <td>Enter Student image</td>
          <td>:</td>
          <td>
            <input
              value={data.StudentImage}
              type="text"
              onChange={(e) => {
                setdata({ ...data, StudentImage: e.target.value });
              }}
            />
          </td>
        </tr>
        <tr>
          <td>Enter Student name</td>
          <td>:</td>
          <td>
            <input
              value={data.StudentName}
              type="text"
              onChange={(e) => {
                setdata({ ...data, StudentName: e.target.value });
              }}
            />
          </td>
        </tr>
        <tr>
          <td>Enter Student's Mobile number</td>
          <td>:</td>
          <td>
            <input
              value={data.StudentMobile}
              type="text"
              onChange={(e) => {
                setdata({ ...data, StudentMobile: e.target.value });
              }}
            />
          </td>
        </tr>
        <tr>
          <td>Enter Parent's mobile number</td>
          <td>:</td>
          <td>
            <input
              value={data.StudentParentMobile}
              type="text"
              onChange={(e) => {
                setdata({ ...data, StudentParentMobile: e.target.value });
              }}
            />
          </td>
        </tr>
        <tr>
          <td>Enter email</td>
          <td>:</td>
          <td>
            <input
              value={data.StudentEmail}
              type="text"
              onChange={(e) => {
                setdata({ ...data, StudentEmail: e.target.value });
              }}
            />
          </td>
        </tr>
        <tr>
          <td>Enter Department</td>
          <td>:</td>
          <td>
            <input
              type="text"
              value={data.StudentDepartment}
              onChange={(e) => {
                setdata({ ...data, StudentDepartment: e.target.value });
              }}
            />
          </td>
        </tr>
        <tr>
          <td colSpan="3" align="center">
            <button
              class="btn-hoverE"
              onClick={() => {
                if (params.id > 0) {
                  fetch(
                    "https://62da4aad9eedb699636a73fe.mockapi.io/Student/" +
                      params.id,
                    {
                      method: "PUT",
                      body: JSON.stringify(data),
                      headers: {
                        "Content-type": "application/json"
                      }
                    }
                  ).then(() => {
                    nevigate("/students");
                  });
                } else {
                  fetch(
                    "https://62da4aad9eedb699636a73fe.mockapi.io/Student/",
                    {
                      method: "POST",
                      body: JSON.stringify(data),
                      headers: {
                        "Content-type": "application/json"
                      }
                    }
                  ).then(() => {
                    nevigate("/students");
                  });
                }
              }}
            >
              {params.id > 0 && "Edit "}
              {!params.id > 0 && "Add "}
              Student
            </button>
          </td>
        </tr>
      </table>
      <br />
      <br />
      <br />
      <br />
      <br />
      <br />
      <br />
    </>
  );
}
