import { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import Helmet from "react-helmet";

const mystyle = {
  backgroundColor: "rgba(19, 5, 240, 0.3)",
  padding: "10px",
  width: "24em",
  borderRadius: "0.6em",
  margin: "1em",
  overflow: "hidden",
  cursor: "pointer",
  boxShadow:
    "0 13px 27px -5px hsla(240, 30.1%, 28%, 0.25), 0 8px 16px -8px hsla(0, 0%, 0%, 0.3), 0 -6px 16px -6px hsla(0, 0%, 0%, 0.03)",
  transition: "all ease 200ms",
  marginLeft: "45px"
};

function Student() {
  const [students, setStudents] = useState([]);
  const [q, setQ] = useState("");
  const [searchParam] = useState(["StudentDepartment", "StudentName"]);
  useEffect(() => {
    fetch("https://62da4aad9eedb699636a73fe.mockapi.io/Student")
      .then((res) => {
        return res.json();
      })
      .then((res) => {
        setStudents(res);
      });
  }, []);
  function Search(students) {
    return students.filter((student) => {
      return searchParam.some((newItem) => {
        return (
          student[newItem].toString().toLowerCase().indexOf(q.toLowerCase()) >
          -1
        );
      });
    });
  }

  return (
    <>
      <div class="row">
        <div class="search">
          <label htmlFor="search-form">
            Search Student: &nbsp;
            <input
              type="search"
              name="search-form"
              id="search-form"
              className="search-input"
              placeholder="by name or department"
              value={q}
              onChange={(e) => setQ(e.target.value)}
            />
          </label>
        </div>
        {Search(students).map((fac) => (
          <div class="card col-md-3" style={mystyle}>
            <Helmet>
              <style>
                {
                  'body { background-image: url("https://img.freepik.com/free-vector/blue-pink-halftone-background_53876-99004.jpg?w=2000"); }'
                }
              </style>
            </Helmet>
            <Link
              style={{ color: "black", textDecoration: "none" }}
              to={"../student/" + fac.id}
            >
              <img
                class="card-img-top"
                src={fac.StudentImage}
                alt="Card image cap"
              />

              <div class="card-body">
                <h4 class="card-title">Name: {fac.StudentName}</h4>
                <h4 class="card-text">Department: {fac.StudentDepartment}</h4>
              </div>
            </Link>
          </div>
        ))}
      </div>
    </>
  );
}

export default Student;
